































	var dateUtils = require('../../common/util.js').dateUtils;

	export default {
		data() {
			return {
				bannerData: [],
				listData: [],
				reload: false
			}
		},
		onLoad() {
            uni.showLoading();
			this.getBanner();
			this.getList();
		},
		onPullDownRefresh() {
			this.reload = true;
			this.last_id = "";
			this.getBanner();
			this.getList();
		},
		onReachBottom() {
			this.getList();
		},
		methods: {
			getBanner() {
				uni.request({
					url: this.$requestUrl+'get_comic_banner',
					success: (data) => {
						uni.stopPullDownRefresh();
                        let bannerInfo = data.data;
						if (bannerInfo.status == 1) {
							this.bannerData = bannerInfo.data;
						}
					},
					fail: (data, code) => {
						console.log('fail' + JSON.stringify(data));
					}
				})
			},
			getList() {
                let data = {};
				uni.request({
					url: this.$requestUrl+'get_comic_list',
					data: data,
					success: (data) => {
                        let comicInfo = data.data;
						if (comicInfo.status == 1) {
							let list = comicInfo.data
							this.listData = this.reload ? list : this.listData.concat(list);
							this.reload = false;
						}
					},
					fail: (data, code) => {
						console.log('fail' + JSON.stringify(data));
					},
                    complete: () => {
                    	uni.hideLoading();
                    }
				})
			},
			goDetail: function (e) {
				let detail = {
					id: e.id,
					name: e.name,
					cover: e.cover,
					release_type_name: e.release_type_name,
					created_at: e.created_at
				}
				uni.navigateTo({
					url: "../comic-detail/comic-detail?detailData=" + JSON.stringify(detail)
				})
			},
		},
	}



